body{
    margin: 0;
    overflow-y: scroll;
}
nav{
    background-color: black;
    height: 80px;
    box-shadow: 0px 2px 20px gray;
}
.logo{
    height: 70px;
    width: 70px;
}
#search-bar {
    display: block;
    margin: 50px auto;
    text-align: center;
    width: 20%;
    height: 30px;
    border-radius: 10px;
    font-size: 25px;
}
.card-container {
    margin: 10px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1%;
}
.card {
    list-style: none;
    border: solid 1px black;
    border-radius: 5px;
    box-shadow: inset 2px 2px 10px gray;
}
img {
    width: 80%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    border-radius: 5px;
    border: 1px solid black;
}
h1{
    text-align: center;
    font-family: "Bowlby One";
    font-size: 35px;
    font-style: normal; 
    font-variant: normal;
    font-weight: 600;
    letter-spacing: 8px;
}
h2{
    text-align: center;
    font-family: "Bowlby One";
    font-size: 25px;
    font-style: normal; 
    font-variant: normal;
    font-weight: 400;
    text-decoration: underline;
    letter-spacing: 3px;
}
h3 {
    text-align: center;
    font-family: "Shadows into Light";
    font-size: 22px;
    font-weight: 600;
}
h4 {
    text-align: center;
    font-family: "Shadows into Light";
    font-size: 15px;
    font-weight: 600;
}
span {
    color: green;
}
.ingredients{
    margin-left: 5%;
    list-style: square;
}
.more-button{
    display: block;
    margin: 20px auto;
    background-color: rgb(215, 238, 245);
    border-radius: 5px;
    cursor: pointer;
    padding: 5px 10px;
}
p {
    padding: 10px;
}
.error {
    color: red;
    font-size: 25px;
    text-align: center;
    margin-top: 150px;
}
a {
    margin-left: 10%;
    margin-bottom: 10px;
    color: black;
}
.to-top {
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 10;
}
.scroll-to-top {
    background-color: rgb(215, 238, 245);
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
}